@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;1,600&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #FF7A7A;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}
body header .desktop {
  display: none;
}
@media (min-width: 71.875em) {
  body header .desktop {
    display: block;
  }
}
body header .mobile {
  display: block;
}
@media (min-width: 71.875em) {
  body header .mobile {
    display: none;
  }
}
body header img.mobile {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}
body main.container {
  max-width: 1105px;
  width: 86%;
}
body main.container .main-text {
  margin-top: 6.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 3.75rem;
}
body main.container .main-text h1 {
  font-weight: 700;
  font-size: 1.6875rem;
  line-height: 1.983125rem;
  text-align: center;
  color: white;
  margin-bottom: 2.1875rem;
}
body main.container .main-text p {
  font-weight: 400;
  font-size: 0.99375rem;
  line-height: 1.64rem;
  text-align: center;
  color: white;
  opacity: 0.76;
}
body main.container .right-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}
body main.container .right-col .banner {
  width: 100%;
  background-color: #6055A5;
  box-shadow: 0px 9px 5px rgba(62, 60, 73, 0.37);
  border-radius: 0.8125rem;
  padding: 1.75rem 0 1.75rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.5625rem;
}
body main.container .right-col .banner h2 {
  font-weight: 400;
  font-size: 1.0625rem;
  line-height: 1.64375rem;
  text-align: center;
  color: white;
  width: 200px;
}
body main.container .right-col .banner h2 strong {
  font-weight: 700;
}
body main.container .right-col .form-container {
  background-color: white;
  border-radius: 0.6875rem;
  box-shadow: 1px 9px 5px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  align-items: center;
}
body main.container .right-col .form-container form {
  margin: 1.5625rem 0 0.9375rem 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
body main.container .right-col .form-container form label {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
body main.container .right-col .form-container form .form-item {
  display: flex;
  flex-direction: column;
  width: 85.8%;
  position: relative;
}
body main.container .right-col .form-container form .form-item input {
  height: 3.5rem;
  margin: 0.5rem 0 0.5rem 0;
  padding-left: 1.125rem;
  border: 1px solid rgba(62, 60, 73, 0.17);
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 1.3125rem;
  color: #3E3C49;
  transition: border 0.2s ease-in-out;
}
body main.container .right-col .form-container form .form-item input:hover,
body main.container .right-col .form-container form .form-item input:focus {
  border: 1px solid #3E3C49;
  outline: none;
  transition: border 0.2s ease-in-out;
}
body main.container .right-col .form-container form .form-item input.error {
  border: 1px solid #FF7A7A;
  color: #FF7A7A;
}
body main.container .right-col .form-container form .form-item img {
  position: absolute;
  z-index: 5;
  left: 85%;
  top: 25%;
  display: none;
}
body main.container .right-col .form-container form .form-item img.error {
  display: block;
}
body main.container .right-col .form-container form .form-item span {
  align-self: flex-end;
  font-weight: 600;
  font-style: italic;
  font-size: 0.6875rem;
  line-height: 1.03125rem;
  color: rgba(255, 122, 122, 0.73);
}
body main.container .right-col .form-container form .submit {
  width: 85.8%;
  height: 3.5rem;
  margin-top: 0.625rem;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.5rem;
  text-align: center;
  color: rgba(255, 255, 255, 0.83);
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  box-shadow: 0px 3px 2px #1DA56A;
  border: none;
  cursor: pointer;
  transition: background-color 0.25s ease-in-out;
}
body main.container .right-col .form-container form .submit:hover,
body main.container .right-col .form-container form .submit:focus {
  background-color: #77E2B4;
  transition: background-color 0.25s ease-in-out;
}
body main.container .right-col .form-container form p {
  font-weight: 400;
  font-size: 0.6875rem;
  line-height: 1.36125rem;
  text-align: center;
  color: rgba(185, 182, 211, 0.8);
  width: 85.8%;
  margin-bottom: 20px;
}
body main.container .right-col .form-container form p a {
  font-weight: 700;
  color: #FF7A7A;
  transition: color 0.15s ease-in-out;
  text-decoration: none;
}
body main.container .right-col .form-container form p a:hover,
body main.container .right-col .form-container form p a:focus,
body main.container .right-col .form-container form p a:visited {
  color: #FF2626;
  transition: color 0.15s ease-in-out;
}